<template>
  <div class="square">
    <div class="list">
      <!--投票-->
      <div class="vote" v-show="state == 2">
        <span class="vote_type_span">投票类型</span>
        <div class="vote_type">
          <span
            class="vote_type_left"
            @click="voteClick(1)"
            :class="voteType==1?'vote_left_color':''"
          >车型投票</span>
          <span
            class="vote_type_right"
            @click="voteClick(2)"
            :class="voteType==2?'vote_left_color':''"
          >自定义投票</span>
          <span
            class="vote_type_right"
            @click="voteClick(3)"
            :class="voteType==3?'vote_left_color':''"
          >品牌投票</span>
        </div>
      </div>
      <!--指定车主-->
      <van-popup
        position="right"
        id="id"
        :style="{height:'100%',width:'80%'}"
        v-model="formulateShow"
      >
        <div class="popup">
          <div class="popup_header" @click="showClick">
            <img class="popup_header_img" src="../../assets/images/left.png" alt />
            <span class="popup_header_span">选择品牌</span>
          </div>
          <div class="popup_content">
            <div class="popup_content_kuai" v-for="(item,index) in list" :key="index">
              <van-index-anchor ref="indexes" class="popup_content_title">{{index}}</van-index-anchor>
              <div
                class="popup_content_text"
                @click="appointClick(item1.name,item1.id)"
                v-for="(item1,key) in item"
                :key="key"
              >
                <img :src="item1.icon" class="popup_content_text_img" alt />
                <span class="popup_content_text_span">{{item1.name}}</span>
              </div>
            </div>
          </div>
        </div>
      </van-popup>
      <!--问答 ABD字母-->
      <!-- <div class="letter" v-show="show">
        <span
          class="letter_span"
          @click="indexesClick(index)"
          v-for="(item,index) in list"
          :key="index"
        >{{index}}</span>
      </div> -->
      <div class="money">
        <div class="money_up">
          <span class="money_up_left">选择打赏金额</span>
          <span class="money_up_right">提高额度，吸引高手前来回答</span>
        </div>
        <div class="money_down">
          <div class="money_down_left">
            <span
              @click="colorClick(index,item.name)"
              class="span"
              v-for="(item,index) in items"
              :key="index"
              :class="isColor == index?'money_down_left_color':''"
            >{{item.name}}元</span>
            <!-- <span class="span">1元</span>
                  <span class="span">2元</span>
                  <span class="span">3元</span>
                  <span class="span">4元</span>
            <span class="span">5元</span>-->
          </div>
          <input
            class="money_down_right"
            onKeyUp="this.value=this.value.replace(/[^\.\d]/g,'');"
            @input="inputClick"
            type="number"
            placeholder="自定义"
          />
        </div>
      </div>
      <div class="content">
        <span class="content_title">填写问题</span>
        <textarea
          v-model="payfeeVote.message"
          class="content_textarea"
          name
          placeholder="试试描述一下你的问题,让车友帮你出谋划策..."
          id
          cols="30"
          rows="10"
        ></textarea>
        <van-uploader v-model="fileList" :max-count="9" multiple />
      </div>
      <!--投票选项-->
      <div class="option">
        <div class="option_header">
          <span class="option_header_left">投票选项</span>
          <div class="option_header_right">
            <span class="option_header_right_span">选择距截至时间</span>
            <!-- <van-count-down :time="timeValue" /> -->
            <span class="option_header_right_time" @click="timeClick">{{timeValue}}</span>
          </div>
        </div>
        <div class="selsect">
            <div class="selsect_span">
               <span class="selsect_single" @click="selsectSateClick(1)" :class="selsect==1?'selsect_state':''">单选</span>
               <span class="selsect_single" @click="selsectSateClick(2)" :class="selsect==2?'selsect_state':''">多选</span>
               <span class="selsect_single" v-show="selsect==2" @click="selsectSateModelClick">{{selsectSateModel}}</span>
               <van-popup v-model="selsectSateShow" position="bottom">
                   <van-picker show-toolbar :columns="columnst" @confirm="onChange" />
               </van-popup>
            </div> 
            <span class="selsect_text" v-show="selsect==1">单选——投票者只能选择一个选项进行投票</span>
            <span class="selsect_text" v-show="selsect==2">多选——投票者可选择多个选项进行投票</span>
        </div>
        <div class="addVehicle">
            <!--自定义-->
            <div class="custom" v-show="voteType==2">
                <div class="custom_state">
                    <span class="custom_state_span" @click="customStateClick(1)" :class="customState==1?'customStateSelect':''">文字模式</span>
                    <span class="custom_state_span" @click="customStateClick(2)" :class="customState==2?'customStateSelect':''">图文模式</span>
                </div>
                <!--图片模式-->
                <div class="imgPattern" v-show="customState==2" v-for="(item3,index3) in imgTextItem" :key="index3">
                    <img :src="item3.isShow?'../../assets/images/icon_user_normal.png':'../../assets/images/icon_user_selecte.png'" class="imgPattern_left_img" alt="">
                    <div class="imgPatterniIamge" ref="file">
                        <input type="file"  @change="fileChange" class="file">
                        <img :src="item3.images" class="file_image" alt="">
                    </div>
                    <input type="text" class="imgPattern_span" v-model="item3.text" placeholder="请输入文字">
                    <img @click="imgPatternClicik(item3.id)" src="../../assets/images/imgPattern_span.png" class="imgPattern_image_right" alt="">
                </div>
                <div class="add_custom" @click="addAutomobile(3)">+添加选项</div>
            </div>
        </div>
      </div>
      <!--投票车辆类型-->
      <van-popup position="right"  id="ids" :style="{height:'100%',width:'80%'}" v-model="addVehicleShow">
        <div class="popup">
          <div class="popup_header" @click="vehicleClick">
            <img class="popup_header_img" src="../../assets/images/left.png" alt />
            <span class="popup_header_span">选择品牌</span>
          </div>
          <div class="popup_content">
            <div class="popup_content_kuai" v-for="(item,index) in list" :key="index">
              <van-index-anchor ref="indexesVehicl" class="popup_content_title">{{index}}</van-index-anchor>
              <div
                class="popup_content_text"
                @click="twoClick(item1.id,2)"
                v-for="(item1,key) in item"
                :key="key"
              >
                <img :src="item1.icon" class="popup_content_text_img" alt />
                <span class="popup_content_text_span">{{item1.name}}</span>
              </div>
            </div>
          </div>
        </div>
      </van-popup>
      <!--投票er级选择-->
      <van-popup position="right" :style="{height:'100%',width:'80%'}" v-model="addErVehicleShow">
        <div class="popup">
          <div class="popup_header" @click="vehicleClick">
            <img class="popup_header_img" src="../../assets/images/left.png" alt />
            <span class="popup_header_span">选择车系</span>
          </div>
          <div class="popup_content">
            <div
              class="popup_content_vehicle"
              @click="addVehicleClick(item2.id,item2.icon,item2.name)"
              v-for="(item2,index) in vehicle"
              :key="index"
            >
              <img
                v-show="item2.show"
                class="icon_image"
                src="../../assets/images/icon_user_selecte.png"
                alt
              />
              <img :src="item2.icon" class="popup_content_vehicle_img" alt />
              <span class="popup_content_vehicle_span">{{item2.name}}</span>
            </div>
          </div>
        </div>
      </van-popup>
      <!--投票字母-->
      <div class="letter" v-show="addVehicleShow">
        <span
          class="letter_span"
          @click="indexVehiclClick(index)"
          v-for="(item,index) in list"
          :key="index"
        >{{index}}</span>
      </div>
      <!--打赏方式-->
      <div class="rewardMode" v-show="$store.state.isAdmin == 0 && state==2">
        <span class="rewardMode_type_span">打赏方式</span>
        <div class="rewardMode_type">
          <span
            class="rewardMode_type_left"
            @click="rewardModeClick(1)"
            :class="rewardMode==1?'rewardMode_left_color':''"
          >10人随机均分</span>
          <span
            class="rewardMode_type_right"
            @click="rewardModeClick(2)"
            :class="rewardMode==2?'rewardMode_left_color':''"
          >20人随机均分</span>
        </div>
      </div>
      <div v-show="$store.state.isAdmin == 1">
        <div class="rewardAdmin">
          <span class="rewardAdmin_span">打赏方式</span>
          <span class="rewardAdmin_select" @click="confirmReward">{{confirmRewardSpan}}</span>
          <span class="rewardAdmin_number" @click="columnsNumberClick">{{cancelRewardTime}}人</span>
        </div>
        <div class="rewardAdmin">
          <span class="rewardAdmin_span">投票用户</span>
          <span class="rewardAdmin_brand" @click="addAutomobile(2)">指定品牌</span>
          <span
            class="rewardAdmin_brand"
            v-show="rewardAdminBrandName != ''"
          >{{rewardAdminBrandName}}</span>
        </div>
      </div>
      <div class="notice">
        <img @click="isShowClick" class="notice_img" :src="isShow?choice:select" alt />
        <span class="notice_span">
          我已阅读并同意
          <i class="notice_span_bt">《用户须知》</i>
        </span>
      </div>
      <!-- <a v-if="mweb_url" :href="mweb_url">发布</a> -->
      <div class="buttom" @click="releaseClick">
        <span class="buttom_text">发布问题</span>
        <span class="buttom_span">￥{{payfeeVote.price}}</span>
      </div>
    </div>
    <van-popup v-model="columnsNumberShow" position="bottom">
      <van-picker
        show-toolbar
        :columns="columnsNumber"
        @cancel="cancelRewardSpan"
        @confirm="confirmRewardClick"
      />
    </van-popup>
    <van-action-sheet v-model="rewardAdminShow" :actions="actions" @select="onSelect" />
    <van-popup v-model="time" position="bottom">
      <van-picker show-toolbar :columns="columns" @cancel="cancel" @confirm="confirm" />
    </van-popup>
  </div>
</template>
<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      imgTextItem:[],
      customState:1, //1 文字模式 2.图文模式
      selsectSateModel:2,  //初始化多选人数
      selsect:'1', //选择单选 多选
      selsectSateShow:false,
      mweb_url:'',
      rewardAdminBrandName: "", //2.管理员添加品牌名称
      typeAdmin: 1, //1.添加车型 2.管理员添加品牌
      columnsNumberShow: false, //管理员打赏方式人数 显示隐藏
      columnsNumber: ["10", "20", "30", "40"], //管理员打赏方式人数
      confirmRewardSpan: "随机均分", //管理员打赏方式
      rewardAdminShow: false, //管理员打赏方式 显示隐藏
      rewardMode: 1, //打赏方式 1. 10  2.20
      addVehicleShow: false, //添加车型 显示隐藏
      addErVehicleShow: false,
      time: false, //时间显示隐藏
      timeValue: "24:00:00",
      columnst:["2","3","4","5","6","7","8"], //人数
      columns: ["24小时", "48小时", "72小时", "96小时", "120小时", "144小时"], //投票选择距截止时间
      voteType: 1, //车型投票，自定义投票选择
      state: 2, //1 问答 2投票
      fileList: [],
      items: [
        //金额
        { name: "0" },
        { name: "1" },
        { name: "2" },
        { name: "3" },
        { name: "4" },
        { name: "5" }
      ],
      isShow: false,
      text: "选择车型",
      isColor: "",
      show: false, //以及
      vehicleShow: false, //二级
      formulateShow: false, //制定
      appointItem: [], //指定车主 集合
      appointItemId: [], //指定车主id 集合
      select: require("../../assets/images/icon_user_selecte.png"),
      choice: require("../../assets/images/icon_user_normal.png"),
      list: [], // 车型
      vehicle: [],
      item: {
        token: this.$store.state.userData,
        upid: 0,
        type: ""
      },
      payfeeVote: {
        //支付金额 投票
        token: this.$store.state.userData,
        message: "",
        price: "0",
        polloption: {}
      },
      parameters: {
        //调起支付后 提交的参数 投票
        token: this.$store.state.userData,
        isimage: "",
        price: "",
        message: "",
        aid_list: "",
        attachment: "",
        expiration: "",
        polloption: {},
        answer: "",
        reward_type: "0",
        reward_num: "10",
        out_trade_no: "",
        prepay_id: ""
      },
      cancelRewardTime: "10", //管理员选择人数
      actions: [], //管理员打赏 答案选择
      id: 1, //自定义投票选项 ID
      aid_list: [] //上出图片返回的ID
    };
  },
  mounted() {
    this.item.upid = 0;
    this.item.type = "";
    this.api.getCars(this.item).then(res => {
      if (res.err_code == 0) {
        this.list = res.data;
      }
    });
  },
  methods: {
    imgPatternClicik(id) {
      this.imgTextItem = this.imgTextItem.filter(v => v.id != id);
    },
    //图文上传图片
    fileChange(e) {
      let file = e.target.files[0];
      let reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onloadend = (ev) => {
          // 赋值给vue对象属性
          this.images = ev.target.result
        };
    },
      //文字模式和图文模式选择
    customStateClick(i) {
        this.customState = i
    },
      //选择单选 多选 选择人数
    selsectSateModelClick() {
        this.selsectSateShow = true
    },
    //选择单选 多选
    selsectSateClick(i) {
        this.selsect = i;
    },
    //管理员选择人数 显示隐藏
    columnsNumberClick() {
      this.columnsNumberShow = true;
    },
    //管理员选择人数
    confirmRewardClick(i) {
      this.parameters.reward_num = i;
      this.cancelRewardTime = i;
      this.columnsNumberShow = false;
    },
    //管理员选择人数 显示隐藏
    cancelRewardSpan() {
      this.columnsNumberShow = false;
    },
    //管理员选择打赏方式
    onSelect(v) {
      this.rewardAdminShow = false;
      this.confirmRewardSpan = v.name;
      this.parameters.reward_type = v.id;
    },
    //管理员打赏方式
    confirmReward() {
      this.rewardAdminShow = true;
      this.actions = [
        { name: "随机均分", id: 1 },
        { name: "正确答案随机均分", id: 2 }
      ];
    },
    //时间确定
    confirm(value) {
      // console.log(value);
      let time = value.substring(0, 2);
      if (time == 24) {
        this.timeValue = "24:00:00";
      } else if (time == 48) {
        this.timeValue = "48:00:00";
      } else if (time == 72) {
        this.timeValue = "72:00:00";
      } else if (time == 96) {
        this.timeValue = "96:00:00";
      } else if (time == 12) {
        this.timeValue = "120:00:00";
      } else if (time == 14) {
        this.timeValue = "144:00:00";
      }
      // this.timeValue = time* 60 * 60*1000;
      this.time = false;
    },
    //时间显示取消
    cancel() {
      this.time = false;
    },
    //时间显示
    timeClick() {
      this.time = true;
    },
    //车型投票，自定义投票选择
    voteClick(i) {
      this.voteType = i;
    },
    // 投票 打赏方式
    rewardModeClick(i) {
      this.rewardMode = i;
      if (this.$store.state.isAdmin == 1) {
        if (i == 1) {
          this.parameters.reward_num = 10;
        } else {
          this.parameters.reward_num = 20;
        }
      }
    },
    //返回上一页
    onClickLeft() {
      this.$router.go(-1);
    },
    //上传图片
    postImg() {
      if (this.aid_list.length == 3) {
        this.$toast("最多上传3张");
        return false;
      }
      return new Promise((resolve, reject) => {
        const promises = this.fileList.map(v => {
          let formData = new FormData();
          formData.append("token", this.$store.state.userData);
          formData.append("myfile", v.file);
          formData.append("type", "question");

          return this.api.addImage(formData).then(res => {
            if (res.err_code == 0) {
              this.aid_list.push(res.data.aid);
            } else {
            }
          });
        });
        Promise.all(promises)
          .then(function() {
            resolve();
          })
          .catch(function(reason) {});
      });
    },
    // 多选选择人数
    onChange(value) {
        this.selsectSateModel = value
        this.selsectSateShow = false
    },
    //须知
    isShowClick() {
      this.isShow = !this.isShow;
    },
    //金额选择
    colorClick(index, item) {
      this.isColor = index;
      this.payfeeVote.price = item;
    },
    //自定义价格
    inputClick(e) {
      // console.log(e);
      this.payfeeVote.price = e.target.value;
      if (e.target.value != "") {
        this.isColor = 99999;
      } else {
        this.isColor = 0;
      }
    },
    //选择车辆
    showPopup() {
      this.show = true;
    },
    //问答字母滑动
    indexesClick(index) {
      let id = document.querySelector("#id");
      for (let i = 0; i < this.$refs.indexes.length; i++) {
        if (this.$refs.indexes[i].$vnode.elm.innerText == index) {
          id.scrollTop = this.$refs.indexes[i]._vnode.elm.offsetTop;
        }
      }
    },
    //选择车类型
    twoClick(id, state) {
      this.item.upid = id;
      this.item.type = "";
      this.api.getCars(this.item).then(res => {
        if (res.err_code == 0) {
          this.vehicle = res.data;
        }
      });

        //投票
        this.addVehicleShow = false;
        this.addErVehicleShow = true;
      
    },
    //返回车中
    vehicleClick() {
      this.vehicleShow = false;
      this.show = true;
      this.addVehicleShow = false;
    },
    //隐藏车系
    showClick() {
      this.show = false;
    },
    //选择车型
    vehicleItemClick(name, id) {
      this.text = name;
      this.parameter.car_2 = id;
      this.vehicleShow = false;
    },
    //指定车主
    appointClick(name, id) {
      let data = {
        name: name,
        isShow: true,
        id: id
      };

      if (this.appointItem.length == 20) {
        this.$toast("指定车型最多20个");
        return false;
      }
      for (let i = 0; i < this.appointItem.length; i++) {
        if (this.appointItem[i].id == id) {
          return false;
        }
        //console.log(this.appointItemId);
      }
      this.appointItem.push(data);
      this.appointItemId.push(id);
    },
    //投票字母滑动
    indexVehiclClick(index) {
      let id = document.querySelector("#ids");
      for (let i = 0; i < this.$refs.indexesVehicl.length; i++) {
        if (this.$refs.indexesVehicl[i].$vnode.elm.innerText == index) {
          id.scrollTop = this.$refs.indexesVehicl[i]._vnode.elm.offsetTop;
        }
      }
    },
    //投票选择品牌
    addVehicleClick(id, icon, name) {
      for (let i = 0; i < this.vehicle.length; i++) {
        if (id == this.vehicle[i].id) {
          this.$set(this.vehicle[i], "show", true);
        }
      }
      //添加车型
      if (this.typeAdmin == 1) {
        let data = {
          id: id,
          icon: icon,
          name: name,
          isShow: false
        };
        for (let i = 0; i < this.addVehicleItem.length; i++) {
          if (this.addVehicleItem[i].id == id) {
            return false;
          }
        }
        this.addVehicleItem.push(data);
      } else if (this.typeAdmin == 2) {
        this.rewardAdminBrandName = name;
        this.addVehicleShow = false;
      }
    },
    //投票 显示品牌
    addAutomobile(i) {
      this.typeAdmin = i;
     if(i==2) {
        this.addVehicleShow = true;
        //自定义模式
      }else if(i == 3) {
          if(this.customState == 2) {
            this.id = ++this.id;
            let data = {
              id: this.id,
              images: "",
              isShow: false,
              text:'',
            };
            this.imgTextItem.push(data)
          }
      }
    },
    //确定 提交问题
    async releaseClick() {
      await this.postImg();
      if (this.payfeeVote.message == "") {
        this.$toast("请填写发布内容");
        return false;
      }
      //问答
      if (this.state == 1) {
        this.parameter.price = this.payfeeVote.price;
        this.parameter.member_tag = this.appointItemId.join(",");
        this.parameter.message = this.payfeeVote.message;
         if (this.fileList != []) {
                this.parameter.attachment = 2;
                this.parameter.aid_list = this.aid_list.join(",");
              }
        if (this.parameter.price == 0) {
          return null;
          this.api.addQuestion(this.parameter).then(res => {
                if (res.err_code == 0) {
                  // this.$router.go(-1);
                  Toast("发布成功");
                  this.payfeeVote.message = '';
                  this.parameter.message = '';
                  setTimeout(() => {
                    let json = { method: "e_loadPage()" };
                    console.log(
                      `teldapp://closeWebview?params=` + JSON.stringify(json)
                    );
                    window.location.href =
                      `teldapp://closeWebview?params=` + JSON.stringify(json);
                  }, 900);
                } else {
                  Toast(res.err_msg);
                }
              });
        } else {
          this.api.payfee(this.payfeeVote).then(res => {
            if (res.err_code == 0) {
              // this.mweb_url = res.data.mweb_url;
              // return null;
              window.location.href=res.data.mweb_url;
              this.parameter.out_trade_no = res.data.out_trade_no;
              this.parameter.prepay_id = res.data.prepay_id;
              this.api.addQuestion(this.parameter).then(res => {
                if (res.err_code == 0) {
                  // this.$router.go(-1);
                  Toast("发布成功");
                  this.payfeeVote.message = '';
                  this.parameter.message = '';
                  setTimeout(() => {
                    let json = { method: "e_loadPage()" };
                    console.log(
                      `teldapp://closeWebview?params=` + JSON.stringify(json)
                    );
                    window.location.href =
                      `teldapp://closeWebview?params=` + JSON.stringify(json);
                  }, 900);
                } else {
                  Toast(res.err_msg);
                }
              });
            } else {
              Toast(res.err_msg);
            }
          });
        }
      } else {
        //投票
        if (this.addVehicleItem == [] || this.addVehicleItem.length == 0) {
          this.$toast("请选择投票选项");
          return false;
        }
        if (this.addVehicleItem.length < 2) {
          this.$toast("投票选项至少两个");
          return false;
        }
        let obj = {};
        let answer = "";
        this.addVehicleItem.forEach((v, key) => {
          if (this.voteType == 1) {
            obj[key] = v.id;
          } else {
            obj[key] = v.icon;
          }
          if (v.isShow == true) {
            answer = ++key;
          }
        });
        this.payfeeVote.polloption = JSON.stringify(obj);
        this.api.payfeeVote(this.payfeeVote).then(res => {
          if (res.err_code == 0) {
            if (this.voteType == 1) {
              this.parameters.isimage = 2;
            } else {
              this.parameters.isimage = 0;
            }
            this.parameters.price = this.payfeeVote.price;
            this.parameters.message = this.payfeeVote.message;
            this.parameters.aid_list = this.aid_list.join(",");
            this.parameters.attachment = 2;
            this.parameters.polloption = this.payfeeVote.polloption;
            this.parameters.expiration =
              this.timeValue.substring(0, 2) * 60 * 60;
            this.parameters.answer = answer;
            this.parameters.out_trade_no = res.out_trade_no;
            this.parameters.prepay_id = res.prepayid;
            this.api.addQuestionVote(this.parameters).then(res => {
              if (res.err_code == 0) {
                this.$toast(res.data.tip.title);
                setTimeout(() => {
                  let json = { method: "e_loadPage()" };
                  console.log(
                    `teldapp://closeWebview?params=` + JSON.stringify(json)
                  );
                  window.location.href =
                    `teldapp://closeWebview?params=` + JSON.stringify(json);
                }, 900);
                // this.$router.go(-1);
              } else {
                Toast(res.err_msg);
              }
            });
          }
        });
      }
    },
  }
};
